"use client";
import { useRef,useState  } from "react"
import styles from '../../styles/CanvasGrid.module.css';
import { useEffect } from "react";
import { useRouter } from "next/router";

export default function CanvasGrid(props) {
  const canvasContainer = useRef(null);
   const router = useRouter();
   const [overlayVisible, setOverlayVisible] = useState(true);


  const handleOverlayClick = () => {
    setOverlayVisible(false);
    const iframe = canvasContainer.current;

    // Focus the iframe after click
    setTimeout(() => {
      try {
        iframe.focus();
        iframe.contentWindow?.focus();
        console.log("Iframe focused for Minecraft");
      } catch (err) {
        console.warn("Iframe focus failed:", err);
      }
    }, 100);
  };

  useEffect(() => {
    if (canvasContainer.current) {
      canvasContainer.current.setAttribute("tabIndex", "0");
    }
  }, []);

  function fullScreen() {
    handleOverlayClick();
    if (document.fullscreenElement) {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
    } else {
      if (canvasContainer.current.requestFullscreen) {
        canvasContainer.current.requestFullscreen();
      } else if (canvasContainer.current.webkitRequestFullscreen) {
        
        canvasContainer.current.webkitRequestFullscreen();
      } else if (canvasContainer.current.mozRequestFullScreen) {
        canvasContainer.current.mozRequestFullScreen();
      } else if (canvasContainer.current.msRequestFullscreen) {
        canvasContainer.current.msRequestFullscreen();
      }
    }
  }

  function Sandbox(value){
    var sand_value;
    if (value == "") {
      // sand_value = "allow-scripts allow-popups allow-same-origin";
    } else {
      // sand_value =
        // "allow-forms allow-pointer-lock allow-popups-to-escape-sandbox allow-same-origin allow-scripts allow-top-navigation-by-user-activation";
    }
    return sand_value;
  }

  return (
    <div className={styles.thumb_canvas_Grid}>
      <div className="canvas-containerr">
         {/* Black overlay */}
      {overlayVisible && router.asPath.includes("minecraft") && (
        <div  className="game-canvas"
          onClick={handleOverlayClick}
          style={{
           
            width: "100%",
            height: "50px",
            backgroundColor: "rgb(104 66 255)",
            color: "#fff",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 10,
            cursor: "pointer",
          }}
        >
          <p style={{ fontSize: "1.2rem" }}>Click here to enable keyboard ðŸŽ®</p>
        </div>
      )}
    

    {props.brand.includes("Sand")&&( <iframe
          ref={canvasContainer}
          //  sandbox="allow-scripts allow-same-origin"
          className="game-canvas"
          id="game_frame"
          height="600px"
          width="100%"
          frameBorder="0"
          scrolling="no"
          src={props.iframe}
          data-hj-allow-iframe="true"
        ></iframe>)}

 {props.brand!=("Sand")&&( <iframe
          ref={canvasContainer}
           sandbox="allow-scripts allow-same-origin"
          className="game-canvas"
          id="game_frame"
          height="600px"
          width="100%"
          frameBorder="0"
          scrolling="no"
          src={props.iframe}
          data-hj-allow-iframe="true"
        ></iframe>)}

       
        <div className={styles.game_info}>
          <p className="game-title">{props.name}</p>
          <a className="game-fullscreen" onClick={fullScreen}>
            <img alt="Full screen" title="Full screen" src={props.src}></img>
          </a>
        </div>
      </div>
    </div>
  );
}
